/* following colors are named after the ui-kit in order to be used in functions or properties as it */
// stylelint-disable scss/dollar-variable-default
$orange1: #ff7900;
$orange2: #f16e00;
$gray1: #333;
$gray2: #f4f4f4;
$gray3: #555;
$gray4: #ccc;

$primary-official: $orange1;
$primary: $orange2;
$primary2: $orange1;
$secondary: $black;

// Colors
$red: #e70002; // Error
$red-light: #fde5e6;
$yellow: #ffcd0b; // Warning
$yellow-light: #fffae7;
$green: #3de35a; // Success
$green-light: #ecfdef;
$blue: #26b2ff; // Info
$blue-light: #e9f8ff;

$success: $green; // Success
$success-light: $green-light;
$warning: $yellow; // Warning
$warning-light: $yellow-light;
$danger: $red; // Danger
$danger-light: $red-light;
$info: $blue; // Info
$info-light: $blue-light;

$theme-colors: ();
$theme-colors: map-merge(
  (
    "gray1": $gray1,
    "gray2": $gray2,
    "gray3": $gray3,
    "gray4": $gray4,
    "primary": $primary,
    "primary-official": $primary-official,
    "primary2": $primary2,
    "success-light": $success-light,
    "warning-light": $warning-light,
    "danger-light": $danger-light,
    "info-light": $info-light
  ),
  $theme-colors
);

$o-link-arrow-color: $primary-official;

$color-allowed: ($white, $black, $primary, $secondary, $orange1, $orange2, $gray3, $gray4);

$border-color: $gray2 !default;

// Tooltip
$tooltip-bg:                  $gray2 !default;

/* functional icons */
$functional-icons: () !default;
$functional-icons: map-merge(
  (
    "tick-circle":        $success,
    "info":               $info,
    "Warning-important":  $warning,
    "error-severe":       $danger
  ),
  $functional-icons
);
// stylelint-enable scss/dollar-variable-default

// Tables
$table-border-color:          $gray2 !default;

// Form
$medium-form-group-max-width: 450 !default;

// Dropdown
$dropdown-link-hover-color: $gray3 !default;

// Pagination
$pagination-hover-color: $gray3 !default;
$pagination-active-border-color: $gray3 !default;
$pagination-active-item-border-color: $gray3 !default;
