/**
 * Default color
 */
$white: #fff !default;
$black: #000 !default;
$greige: #f9f5f0 !default;

/**
 * Custom Variables used in our SCSS
 */
$site-max-width: 1440px !default;
$site-height-xs: 258px !default;
$site-height-sm: 490px !default;
$site-height-md: 544px !default;
$site-height-lg: 288px !default;
$site-height-xl: 290px !default;
$site-height-xxl: 390px !default;

/* Button */
$btn-max-width: 21.875rem !default;
$btn-min-height-lg: 4.5em !default;

// Browser default font size, used to calculate rem values
$browser-fs: 16 !default;

/**
 * BOOSTED or BOOTSTRAP overwrite variables
 */

// Breadcrumb component
$breadcrumb-bg: unset !default;

// Alert components
$alert-bg-level: -10 !default;
$theme-color-interval: 9% !default;
$alert-padding-y: .9375rem !default;
$alert-padding-x: $alert-padding-y !default;
$alert-padding-x-widescreen: 1.875rem !default; // not used in Boosted, custom style
$alert-margin-bottom: .9375rem !default;
$alert-icon-size: 2rem !default; // custom style

// Feedback component
$feedback-icon-size: 4.688rem !default;

// Grid breakpoints
// We return to Bootstrap default values
$grid-breakpoints: (
  xs: 0,
  sm: 480px,
  md: 736px,
  lg: 960px,
  xl: 1200px,
  xxl: 1440px
) !default;

// Grid containers

// For "not fluid" containers
// Define the maximum width of `.container` for different screen sizes.
// NON fluid containers are not part of the ONE-I guidelines. We should delete the .container class but it's not as simple as it sound
$container-max-widths: () !default;

$container-fluid-margin-widths: (
  xs: 0,
  sm: 0,
  md: 1.5625%,
  lg: 3.125%,
  xl: 3.125%,
  xxl: 45px
) !default;

// Grid Gutter
$grid-gutter-width: 30px !default;
$grid-gutter-widths: (
  xs: $grid-gutter-width,
  sm: $grid-gutter-width,
  md: $grid-gutter-width,
  lg: $grid-gutter-width,
  xl: $grid-gutter-width,
  xxl: $grid-gutter-width
) !default;

/* Spacer utilities, used for helpers on padding and margin for instance */
$spacer:   1.875rem !default; // 30px
$spacers: () !default;
// stylelint-disable scss/dollar-variable-default,function-parentheses-newline-inside
$spacers: map-merge((
  "-5": - ($spacer * 3),     // -90px
  "-4": - ($spacer * 2),     // -60px
  "-3": - ($spacer * 1.5),   // -45px
  "-2": - $spacer,           // -30px
  "-1": - ($spacer * .5),    // -15px
  0: 0,
  1: ($spacer * .5),    // 15px
  2: $spacer,           // 30px
  3: ($spacer * 1.5),   // 45px
  4: ($spacer * 2),     // 60px
  5: ($spacer * 3)      // 90px
),
$spacers
);
// stylelint-enable

/**
 * Buttons
 */
$btn-padding-y:     .8125rem !default; // 14px
$btn-padding-x:     2.8125rem !default; // 45px
$btn-border-width:  1px !default;
$btn-line-height:   1.375rem !default; // 22px

$btn-padding-y-lg:    $btn-padding-y !default;
$btn-padding-x-lg:    $btn-padding-x !default;
$btn-line-height-lg:  $btn-line-height !default;

/**
 * Typography
 * Those are a mix of Bootstrap, Boosted and custom used variables :/
 */

$font-sosh-path:              "https://c.woopic.com/Magic/" !default; // load sosh font from elcos woopic domain
$icon-font-path:              "https://c.woopic.com/fonts/" !default; // we load fonts files from elcos woopic domain
$icons-filepath:               "../fonts" !default; // path to woff file for orange et sosh icons
$font-size-base:              1rem !default; // Assumes the browser default, typically `16px`
$font-size-lg:                $font-size-base !default; // 16px
$font-size-sm:                ($font-size-base * .75) !default; // 12px
$lead-font-weight:            normal !default;
$paragraph-margin-bottom:     ($spacer * .5) !default;
$title-max-width:             px-to-rem(800) !default; // custom style
$title-max-width-xl:          px-to-rem(1000) !default; // custom style
$text-max-width:              37.5rem !default; // custom style
$text-max-width-xl:           px-to-rem(700) !default; // custom style
$font-size-transverse:        1rem !default; // custom style
$line-height-transverse:      (22 / 16) !default; // custom style
$font-size-transverse2:       .875rem !default; // custom style
$line-height-transverse2:     (20 / 14) !default; // custom style
$font-size-transverse3:       .75rem !default; // custom style
$line-height-transverse3:     (18 / 12) !default; // custom style
$accordion-icon-size:         32;

/* Typography configuration for mobile (xs and sm) */
$font-size-mobile:            .875rem !default; // 14px
$line-height-mobile:          (20 / 14) !default;
$h1-font-size-mobile:         ($font-size-base * 1.625) !default; // 26px
$h1-line-height-mobile:       (30 / 26) !default;
$h1-padding-top-mobile:       px-to-rem(4) !default;
$h1-padding-bottom-mobile:    px-to-rem(10) !default;
$h2-font-size-mobile:         ($font-size-base * 1.5) !default; // 24px
$h2-line-height-mobile:       (28 / 24) !default;
$h2-padding-top-mobile:       px-to-rem(5) !default;
$h2-padding-bottom-mobile:    px-to-rem(16) !default;
$h2-no-signature-padding-bottom-mobile: px-to-rem(10) !default;
$h3-font-size-mobile:         ($font-size-base * 1.25) !default; // 20px
$h3-line-height-mobile:       (24 / 20) !default;
$h3-padding-top-mobile:       px-to-rem(5) !default;
$h3-padding-bottom-mobile:    px-to-rem(11) !default;
$h4-font-size-mobile:         ($font-size-base * 1.25) !default; // 20px
$h4-line-height-mobile:       (24 / 20) !default;
$h4-padding-top-mobile:       px-to-rem(5) !default;
$h4-padding-bottom-mobile:    px-to-rem(11) !default;
$h5-font-size-mobile:         ($font-size-base * .875) !default; // 14px @todo : not defined in docs
$h6-font-size-mobile:         ($font-size-base * .875) !default; // 14px @todo : not defined in docs
$lead-font-size-mobile:       1rem !default; // 16px
$lead-line-height-mobile:     (22 / 16) !default;
$small-font-size-mobile:      .75rem !default; // 12px
$small-line-height-mobile:    (18 / 12) !default;

/* Typography configuration for tablet and small desktop (md, lg) */
$font-size:                   ($font-size-base * .875) !default; // 14px
$line-height-base:            (20 / 14) !default; // Bootstrap config
$h1-font-size:                ($font-size-base * 2.125) !default; // 34px
$h1-line-height:              (40 / 34) !default;
$h1-padding-top:              0 !default;
$h1-padding-bottom:           px-to-rem(8) !default;
$h2-font-size:                ($font-size-base * 1.875) !default; // 30px
$h2-line-height:              (34 / 30) !default;
$h2-padding-top:              0 !default;
$h2-padding-bottom:           px-to-rem(15) !default;
$h2-no-signature-padding-bottom: px-to-rem(9) !default;
$h3-font-size:                ($font-size-base * 1.5) !default; // 24px
$h3-line-height:              (28 / 24) !default;
$h3-padding-top:              px-to-rem(4) !default;
$h3-padding-bottom:           px-to-rem(10) !default;
$h4-font-size:                ($font-size-base * 1.375) !default; // 22px
$h4-line-height:              (26 / 22) !default;
$h4-padding-top:              px-to-rem(6) !default;
$h4-padding-bottom:           px-to-rem(10) !default;
$h5-font-size:                ($font-size-base * 1.125) !default; // 18px @todo : not defined in docs
$h6-font-size:                $font-size-base !default; // 16px @todo : not defined in docs
$lead-font-size:              1rem !default; // 16px
$lead-line-height:            (22 / 16) !default;
$small-font-size:             .75rem !default; // 12px
$small-line-height:           (18 / 12) !default;

/* widescreen variables aren't part of bootstrap, they are used in our custom scss */
$font-size-min: .8125rem !default; //13px
$font-size-widescreen:        1rem !default; // 16px
$line-height-widescreen:      (22 / 16) !default;
$h1-font-size-widescreen:     ($font-size-base * 2.625) !default; // 42px
$h1-line-height-widescreen:   (50 / 42) !default;
$h1-padding-top-widescreen:   0 !default;
$h1-padding-bottom-widescreen: px-to-rem(6) !default;
$h2-font-size-widescreen:     ($font-size-base * 2.125) !default; // 34px
$h2-line-height-widescreen:   (40 / 34) !default;
$h2-padding-top-widescreen:   0 !default;
$h2-padding-bottom-widescreen: px-to-rem(14) !default;
$h2-no-signature-padding-bottom-widescreen: px-to-rem(8) !default;
$h3-font-size-widescreen:     ($font-size-base * 1.875) !default; // 30px
$h3-line-height-widescreen:   (34 / 30) !default;
$h3-padding-top-widescreen:   px-to-rem(3) !default;
$h3-padding-bottom-widescreen: px-to-rem(9) !default;
$h4-font-size-widescreen:     ($font-size-base * 1.625) !default; // 26px
$h4-line-height-widescreen:   (30 / 26) !default;
$h4-padding-top-widescreen:   px-to-rem(4) !default;
$h4-padding-bottom-widescreen: px-to-rem(10) !default;
$h5-font-size-widescreen:     ($font-size-base * .875) !default; // 14px @todo : not defined in docs
$h6-font-size-widescreen:     ($font-size-base * .875) !default; // 14px @todo : not defined in docs
$lead-font-size-widescreen:   1.125rem !default; // 18px
$lead-line-height-widescreen: (24 / 18) !default;
$small-font-size-widescreen:  .875rem !default; // 14px
$small-line-height-widescreen: (20 / 14) !default;

// checkbox background images
$custom-checkbox-indicator-icon-crypto: url("data:image/svg+xml;charset=utf8, %3Csvg width='24' height='24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M16.628 9.899c-.232.43-.506.8-.825 1.111-.319.312-.89.838-1.717 1.576-.227.207-.41.39-.548.548-.137.157-.239.302-.306.434-.067.13-.119.26-.156.391a15.51 15.51 0 0 0-.166.69c-.128.698-.527 1.046-1.198 1.046-.35 0-.643-.114-.88-.342-.239-.228-.358-.566-.358-1.016 0-.564.088-1.051.261-1.464a3.7 3.7 0 0 1 .695-1.087c.289-.312.678-.683 1.168-1.113.429-.376.74-.659.93-.85.192-.19.353-.405.483-.639.132-.235.197-.49.197-.764 0-.537-.2-.99-.6-1.36-.398-.368-.914-.552-1.543-.552-.739 0-1.282.186-1.631.558-.35.373-.644.92-.885 1.645C9.32 9.47 8.887 9.85 8.25 9.85c-.376 0-.693-.133-.951-.398s-.388-.552-.388-.86c0-.638.205-1.284.615-1.938.409-.654 1.006-1.196 1.79-1.625.786-.429 1.701-.644 2.748-.644.973 0 1.832.18 2.577.538.744.36 1.32.847 1.725 1.464.406.618.61 1.289.61 2.013 0 .571-.116 1.07-.348 1.5m-3.759 9.319a1.532 1.532 0 0 1-1.046.403c-.41 0-.767-.134-1.072-.398-.306-.265-.458-.636-.458-1.113 0-.422.148-.777.443-1.066a1.5 1.5 0 0 1 1.087-.433c.422 0 .778.145 1.066.433.289.288.433.644.433 1.066 0 .47-.15.84-.453 1.108M12 0C5.372 0 0 5.372 0 12s5.372 12 12 12c6.627 0 12-5.372 12-12S18.627 0 12 0' fill='%23000' fill-rule='evenodd'/%3E%3C/svg%3E") !default;

// tooltip
$tooltip-opacity:             1 !default;
$tooltip-font-size:           $font-size-base !default;
$tooltip-font-weight:         400 !default;
$tooltip-max-width:           25.25rem !default;
$tooltip-padding-top:         px-to-rem(25) !default;
$tooltip-padding-y:           $spacer !default;
$tooltip-padding-x:           $spacer !default;
$tooltip-margin:              1rem !default;

// modal
$modal-backdrop-bg: $black !default;
$modal-backdrop-opacity: .6 !default;

// push
$push-spacer: px-to-rem(10) !default;
$push-spacer-md: ($spacer * .5) !default;
$push-spacers: (
  xs: $push-spacer,
  sm: $push-spacer,
  md: $push-spacer-md,
  lg: $push-spacer-md,
  xl: $push-spacer-md,
  xxl: $push-spacer-md
) !default;
$position-and-opposite: (
  left: right,
  right: left
) !default;

// Borders
$border-width: .0625rem !default; //1px

// Stepbar
$step-circle-width-sm: 1.5rem !default; //24px
$step-circle-width-md: 2.125rem !default; //34px
$step-circle-width-xl: 2.75rem !default; //44px
$step-margin-x-circle-sm: .3125rem !default; //5px
$step-margin-x-circle-md: .625rem !default; //10px
$step-margin-x-circle-xl: .9375rem !default; //15px
$step-margin-bottom-circle-sm: .3125rem !default; //5px
$step-margin-bottom-circle-md: .625rem !default; //10px
$step-margin-bottom-circle-xl: .625rem !default; //10px
$step-padding-top-step-number-sm: .0625rem !default; //1px
$step-padding-top-step-number-md: .4375rem !default; //7px
$step-padding-top-step-number-xl: .688rem !default; //11px
$step-icon-width-xl: 2.75rem !default; //44px

// Filter Chips
$filterchip-padding-top: px-to-rem(4) !default;
$filterchip-selected-padding-top: px-to-rem(3) !default;
$filterchip-padding-bottom: px-to-rem(6) !default;
$filterchip-selected-padding-bottom: px-to-rem(5) !default;
$filterchip-padding-x: px-to-rem(24) !default;
$filterchip-active-padding-x: px-to-rem(23) !default;
$filterchip-selected-padding-x: px-to-rem(13) !default;

// Dropdown
$dropdown-border-width: 0 !default;
$dropdown-box-shadow: 0 1px 4px 0 rgba(0, 0, 0, .4) !default;
$dropdown-padding-y: px-to-rem(6) !default;
$dropdown-item-padding-y: px-to-rem(9) !default;
$dropdown-item-padding-x: px-to-rem(20) !default;
$dropdown-link-active-color: $black !default;
$dropdown-link-hover-bg: transparent !default;
$dropdown-link-active-bg: transparent !default;

//Pagination
$pagination-min-width: px-to-rem(38) !default;
$pagination-padding-y: px-to-rem(6) !default;
$pagination-padding-x: px-to-rem(2) !default;
$pagination-line-height:  $line-height-transverse !default;
$pagination-border-width: px-to-rem(2) !default;
$pagination-hover-border-color: transparent !default;
$pagination-active-bg: transparent !default;
$pagination-active-item-bg: transparent !default;
$pagination-active-color: $black !default;
$pagination-disabled-border-color: transparent !default;
$pagination-icon-prev-active: url("data:image/svg+xml;charset=utf8, %3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 9 14'%3e%3cpath d='M9 2L7 0 0 7l7 7 2-2-5-5 5-5z' fill='%23000'/%3e%3c/svg%3e") !default;
$pagination-icon-next-active: url("data:image/svg+xml;charset=utf8, %3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 9 14'%3e%3cpath d='M0 2l2-2 7 7-7 7-2-2 5-5-5-5z' fill='%23000'/%3e%3c/svg%3e") !default;
$pagination-icon-next-active-inverse: url("data:image/svg+xml;charset=utf8, %3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 9 14'%3e%3cpath d='M0 2l2-2 7 7-7 7-2-2 5-5-5-5z' fill='%23fff'/%3e%3c/svg%3e") !default;
$pagination-icon-prev-active-inverse: url("data:image/svg+xml;charset=utf8, %3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 9 14'%3e%3cpath d='M9 2L7 0 0 7l7 7 2-2-5-5 5-5z' fill='%23fff'/%3e%3c/svg%3e") !default;
$pagination-icon-next-hover: url("data:image/svg+xml;charset=utf8, %3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 9 14'%3e%3cpath d='M0 2l2-2 7 7-7 7-2-2 5-5-5-5z' fill='%23555'/%3e%3c/svg%3e") !default;
$pagination-icon-prev-hover: url("data:image/svg+xml;charset=utf8, %3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 9 14'%3e%3cpath d='M9 2L7 0 0 7l7 7 2-2-5-5 5-5z' fill='%23555'/%3e%3c/svg%3e") !default;
