@import "./font-icon/variables";

.ob1-panel {
  height: 100%;

  &:hover,
  &.focus-within {
    .ob1-panel-img {
      transition: transform .3s;
      transition-timing-function: ease-out, cubic-bezier(.33, 1, .68, 1);
      transform: scale(1.05);
    }
  }

  &.focus-within {
    outline: .125rem solid $primary;
  }

  &:focus-within {
    outline: .125rem solid $primary;
    .ob1-panel-img {
      transition: transform .3s;
      transition-timing-function: ease-out, cubic-bezier(.33, 1, .68, 1);
      transform: scale(1.05);
    }
  }

  .ob1-panel-mask {
    position: relative;
    overflow: hidden;

    &::before {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 1;
      content: "";
      background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0) 37%, rgba(0, 0, 0, .5));
    }

    .ob1-panel-logo,
    .ob1-panel-csa {
      position: absolute;
      bottom: 0;
      z-index: 1;
    }

    .ob1-panel-logo {
      width: px-to-rem(45);
      height: px-to-rem(45);
      margin-bottom: 10px;
      margin-left: 10px;
    }

    .ob1-panel-csa {
      right: 0;
      margin-right: 10px;
      margin-bottom: 10px;
      font-size: px-to-rem(18);
      color: #fff;
    }

    .ob1-panel-img {
      width: 100%;
      transition: transform .15s;
      transition-timing-function: ease-in, cubic-bezier(.32, 0, .67, 0);
    }
  }

  .ob1-panel-body {
    padding-top: px-to-rem(5);
    .ob1-panel-title {
      padding-bottom: 0;
      margin-bottom: 0;
      font-size: $font-size-mobile;
      line-height: $line-height-mobile;
      a {
        font-size: inherit;
        font-weight: $font-weight-bold;
        &:focus {
          outline: none;
        }
      }
    }

    .ob1-panel-text {
      padding-top: 0;
      font-size: $small-font-size-mobile;
      line-height: $small-line-height-mobile;
      color: $gray3;
    }
  }

  &.ob1-panel-inverse {
    .ob1-panel-body {
      .ob1-panel-title a,
      .ob1-panel-text {
        color: $white;
      }
    }

    &:hover,
    &:focus,
    &:focus-within,
    &.focus-within {
      .ob1-panel-title a,
      .ob1-panel-text {
        color: $gray4;
      }
    }
  }
}

.ob1-csa-10 {
  &::before {
    font-family: icon-orange, sans-serif;
    content: $icon-10;
  }
}

.ob1-csa-12 {
  &::before {
    font-family: icon-orange, sans-serif;
    content: $icon-12;
  }
}

.ob1-csa-16 {
  &::before {
    font-family: icon-orange, sans-serif;
    content: $icon-16;
  }
}

.ob1-csa-18 {
  &::before {
    font-family: icon-orange, sans-serif;
    content: $icon-18;
  }
}

@include media-breakpoint-up(md) {
  .ob1-panel {
    .ob1-panel-mask {
      .ob1-panel-logo {
        width: px-to-rem(60);
        height: px-to-rem(60);
        margin-bottom: 16px;
      }

      .ob1-panel-csa {
        margin-right: 15px;
        margin-bottom: 15px;
        font-size: px-to-rem(24);
        color: #fff;
      }
    }
    .ob1-panel-body {
      padding-top: px-to-rem(10);
      .ob1-panel-title {
        font-size: $font-size-widescreen;
        line-height: $line-height-widescreen;
      }
      .ob1-panel-text {
        font-size: $small-font-size-widescreen;
        line-height: $small-line-height-widescreen;
      }
    }
  }
}
